<template>
  <div>
    <button
      @click="toggleList"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
    >
      {{buttonName}}
    </button>
    <keep-alive>
      <transition name="component-fade" mode="out-in">
        <data-table
          :columns="columns"
          :data="productions.data"
          :actionMethod="fetchProductions"
          :count="productions.count"
          :searchPlaceholder="'Search by Product Name'"
          v-if="showList"
        ></data-table>
        <production-create-view v-else @added="toggleList"></production-create-view>
      </transition>
    </keep-alive>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { DataTable } from 'SHARED/data_table'
import { ProductionCreateView } from '.'
export default {
  name: 'production-list',
  components: {
    DataTable,
    ProductionCreateView
  },
  computed: {
    ...mapGetters({
      productions: 'getProductions'
    }),
    buttonName () {
      if (this.showList) {
        return 'Add'
      } else {
        return 'Back to List'
      }
    }
  },
  methods: {
    ...mapActions([
      'fetchProductions'
    ]),
    toggleList () {
      this.showList = !this.showList
    }
  },
  data () {
    return {
      showList: true,
      columns: [
        {
          field: 'created',
          label: 'Date',
          type: String
        },
        {
          field: 'number_of_hours',
          label: 'Hours',
          type: Number
        },
        {
          field: 'product_quantity',
          label: 'Product Quantity',
          type: String
        },
        {
          field: 'apprx_raw_material_weight',
          label: 'Apprx Weight',
          type: String
        }
      ]
    }
  }
}
</script>



